import selfTaskBox from '@/components/self-task-box.vue';
import selfInput from '@/components/self-input.vue';
import selfButtonRadio from '@/components/self-button-radio.vue';
import selfButton from '@/components/self-button.vue';
import timer from '@/components/timer/index.vue';
import selfUpLoad from '@/components/self-upLoad.vue';

export default {
	data() {
		return {
			detail: {},
			currentPro: '车辆三洗',
			carNo: '浙A 123',
			carUser: '王五',
			dataSource1: [
				{ id: 1, title: '干净' },
				{ id: 2, title: '不干净' }
			],
			active1: 2,
			disinfects: ["A", "B"],
			disinfectIdx: 0,
			kinds: ["A", "B"],
			kindIdx: 0,
			startTime: '',
			endTime: '',
		}
	},
	components: {
		selfTaskBox,
		selfInput,
		selfButtonRadio,
		selfButton,
		timer,
		selfUpLoad
	},
	onLoad(options) {
		const { detail } = options;
		if(detail) {
			this.detail = JSON.parse(detail);
			console.log('-----', this.detail)
		}
	},
	methods: {
		selectHandle1(e) {
			this.active1 = e.id
		},
		disinfectChange(e) {
			this.disinfectIdx = e.target.value
		},
		kindChange(e) {
			this.kindIdx = e.target.value
		},
		startTimeChange(e) {
			this.startTime = e
		},
		endTimeChange(e) {
			this.endTime = e
		},
		changeHandle(imgs) {
			console.log('imgs', imgs)
		},
		okClick() {
			alert('a')
		}
	}
}
